.tb_bt__container{
    width: 100%;
    margin: 0 auto;
}

.tb_bt_popup_wrapper {
    position: fixed;
    height: 100vh;
    width: 100%;
    max-width: 500px;
    top: 0;
    z-index: 9999999;
    background-color: #f3f7fc;
    box-shadow: 0 4px 20px rgba(0,0,0,.1);
}

.tb_bt_post_wrap{
    overflow: auto;
    padding: 10px;
    height: calc(100vh - 75px);
}

.tb_bt_post_wrap::-webkit-scrollbar {
    width: 6px;
}
.tb_bt_post_wrap::-webkit-scrollbar-thumb {
    background: #218eff;
}

.tb_bt_wrapper{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.tb_bt_post_media_wrapp{
    position: relative;
}

.tb_bt_image_wrap_{
    padding-bottom: 100%;
    position: relative;
}
.tb_bt_image {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    width: 100% !important;
    height: 100% !important;
    display: block;
    object-fit: cover;
    object-position: center;
}

.tb_bt_contant_wrapper {
    padding: 5px 0;
    margin-bottom: 3px;
    max-width: 280px;
}

.tb_bt_author_wrapper {
    padding: 10px;
}

.tb_bt_author {
    width: calc(100% - 30px);
}

.tb_bt_authorname {
    font-size: 16px;
    font-weight: 600;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    margin-right: 5px;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_bt_authorname,
.tb_bt_username{
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
}

.tb_bt_author {
    display: flex;
    align-items: start;
    flex-grow: 1;
}

.tb_bt_author_wrapper {
    display: flex;
    align-items: center;
}

.tb_bt_head_title{
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    width: 100%;
    margin: 35px 0 15px;
    text-align: center;
}


.tb_bt_post_info {
    display: flex;
    align-items: start;
    flex-direction: column;
    justify-content: start;
    position: relative;
    min-width: 230px;
}

.tb_bt_close_btn {
    position: absolute;
    right: -20px;
    top: -10px;
    padding: 5px;
    z-index: 1;
    cursor: pointer;
    opacity: .8;
    transition: opacity .3s ease;
}

.tb_bt_close_btn:hover{
    opacity: 1;
}

.tb_bt_close_ico{
    font-size: 16px;
}

.tb_bt_post_in{
    display: flex;
    align-items: start;
    position: relative;
}

.tb_bt_average_sticky.tb_bt_show{
    animation: tb-fade-anim .8s ease;
}

@keyframes tb-fade-anim {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.tb_bt_post_contain.tb_bt_show{
    animation: tb-zoom-anim .8s ease;
}

@keyframes tb-zoom-anim {
    0% {
        transform: scale(1.1);
        opacity: 0;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}
.tb_bt_username {
    font-size: 10px;
    color: #2b2b2b;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_bt_username{
    max-width: 75%;
}

.tb_bt_content {
    font-size: var(--tb-font-size, 14px);
    line-height: 1.5;
    margin-bottom: 15px;
    word-break: break-word;
    white-space: pre-line;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
}

.tb_bt_author_profile {
    height: 80px;
    width: 80px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    border: 3px solid #fff;
    background-color: rgba(0,0,0,.2);
    box-shadow: 0 3px 8px rgba(0,0,0,.15);
    overflow: hidden;
}
.tb_bt_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_bt_author_info {
    overflow: hidden;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    margin-bottom: 3px;
    margin-right: 15px;
}

.tb_bt_network_name{
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
    font-size: 12px;
    font-weight: 500;
    white-space: nowrap;
    padding-left: 4px;
    opacity: .8;
}

.tb_bt_post_wrapper {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
    background: #fff;
    border-radius: 0 15px 15px 0;
    bottom: 50px;
    box-shadow: 2px 3px 10px rgba(0, 0, 0, 0.1);
    left: 0;
    min-width: 195px;
    padding: 12px 20px;
    position: fixed;
    text-align: left;
    width: auto;
    z-index: 99999;
    cursor: pointer;
    transition: all 250ms ease-out;
}

.tb_bt_rating__{
    margin-bottom: 7px;
}

.tb_bt_rating__ .tb__rating__{
    padding: 0;
    font-size: 18px;
}

.tb_bt_rating__ .tb_rating_ico__{
    font-size: 18px;
    margin-right: 2px;
    margin-left: 0;
}

.tb_bt_social_{
    display: flex;
    align-items: center;
    justify-content: start;
    flex-wrap: wrap;
    margin-bottom: 3px;
}

.tb_bt_social_list{
    display: flex;
    align-items: center;
    justify-content: center;
    background: #f9f9f9;
    border-radius: 50%;
    margin-right: 2px;
    height: 26px;
    width: 26px;
    padding: 4px;
}

.tb_bt_avg_social_ico{
    font-size: 16px;
}

.tb_bt_social_ico{    
    font-size: 20px;
}

.tb_bt_popup_close_btn{
    position: absolute;
    right: 10px;
    top: 10px;
    padding: 5px;
    z-index: 1;
    cursor: pointer;
}
.tb_bt_popup_close_ico {
    font-size: 12px;
}

.tb_bt_rating_content{
    text-align: left;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

@media screen and (max-width: 667px) {
.tb_bt_author_profile{
    margin-right: 5px;
    height: 50px;
    width: 50px;
    border: 2px solid #fff;
}

.tb_bt_authorname {
    font-size: 14px;
}
.tb_bt_contant_wrapper {
    max-width: 220px;
    padding: 3px 0;
    font-size: 12px;
}

.tb_bt_rating__ .tb_rating_ico__ {
    font-size: 16px;
}
.tb_bt_social_ico {
    font-size: 14px;
}
}